export default function(ractive, dataKey, storageKey) {
  var initialValue = sessionStorage.getItem(storageKey);
  if (initialValue != null) {
    ractive.set(dataKey, JSON.parse(initialValue));
  }
  ractive.observe(dataKey, function(value) {
    sessionStorage.setItem(storageKey, JSON.stringify(value));
  });
}
