var easingFunctions = require('easing-utils');

export default function animate(el, prop, initialValue, finalValue, duration, easingFunction) {
  var start = null;
  function step(timestamp) {
    if (!start) start = timestamp;
    var progress = timestamp - start;
    var valueDelta = (finalValue - initialValue) * easingFunctions[easingFunction](progress / duration);
    el[prop] = initialValue + valueDelta;
    if (progress < duration) {
      window.requestAnimationFrame(step);
    }
  }
  window.requestAnimationFrame(step);
}
